@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
*{box-sizing:border-box}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#FAFAFA;background-color:#212121}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}select{color:white;background-color:#263238;padding:5px}a{color:#03A9F4}

.app-container{margin:10vh auto;width:90vw;max-width:1000px}@media screen and (max-width: 800px){.app-container{margin:20px auto}}.app-container>*{width:100%;margin-bottom:20px}.app-container>.controls{display:-webkit-flex;display:flex}.app-container>.controls>select.lang{width:100%;margin-right:10px}.app-container>.controls>button.run{color:#03A9F4;background-color:transparent;border:none;font-size:1em;padding:0 15px;cursor:pointer;transition:all .15s ease}.app-container>.controls>button.run:hover{background-color:#263238}

.footer{position:absolute;bottom:0;background-color:#263238;width:100%;padding:10px 20px;font-size:0.9em;text-align:center}

.result-viewer{font-family:'Source Code Pro', monospace}.result-viewer>div{padding:10px;background-color:#263238}.result-viewer .heading{font-size:0.8em;color:#90A4AE;margin-bottom:5px}.result-viewer .stderr{color:#f44336}

@-webkit-keyframes spinneranim{0%{width:0%;margin-left:0%}50%{width:100%;margin-left:0%}100%{width:0%;margin-left:100%}}@keyframes spinneranim{0%{width:0%;margin-left:0%}50%{width:100%;margin-left:0%}100%{width:0%;margin-left:100%}}.spinner{width:100%;height:10px}.spinner>div{height:100%;width:0%;background-color:#03A9F4;-webkit-animation:spinneranim 1.5s ease infinite;animation:spinneranim 1.5s ease infinite}

